@import url(https://fonts.googleapis.com/css?family=Raleway:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.day-list {
    border-top: 1px solid black;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}
.day-container {
    align-items: center;
    display: flex;
    flex-direction: column;
    width: calc(100% / 5);
}
.day-container:last-child .day-name,
.day-container:last-child .day-number {
    border-right: none;
}
.day-container > div:not(.active-column) {
    align-items: center;
    display: flex;
    justify-content: center;
    width: 100%;
    min-height: 50px;
}
.day-container:first-child .day-details.bordered {
    border-left: none;
    width: 100%;
}
.day-details.bordered {
    background-color: #464548;
    color: #fff;
    width: calc(100% - 1px);
}
.day-name,
.day-number {
    border-top: 1px solid black;
    border-right: 1px solid black;
}

/* active state */
.active-column {
    background-color: rgba(255, 0, 255, .25);
    position: absolute;
    top: 100px;
    left: 0;
    bottom: 0;
    width: calc(100% / 10);
}
.active-column[data-col-position='1'] {
    left: 0;
}
.active-column[data-col-position='2'] {
    left: calc(100% / 10);
}
.active-column[data-col-position='3'] {
    left: calc(100% / 10 * 2);
}
.active-column[data-col-position='4'] {
    left: calc(100% / 10 * 3);
}
.active-column[data-col-position='5'] {
    left: calc(100% / 10 * 4);
}
.active-column[data-col-position='6'] {
    left: calc(100% / 10 * 5);
}
.active-column[data-col-position='7'] {
    left: calc(100% / 10 * 6);
}
.active-column[data-col-position='8'] {
    left: calc(100% / 10 * 7);
}
.active-column[data-col-position='9'] {
    left: calc(100% / 10 * 8);
}
.active-column[data-col-position='10'] {
    left: calc(100% / 10 * 9);
}
.week-list {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.week-container {
    border-left: 1px solid black;
    border-bottom: 1px solid black;
    width: calc(100%/ 4);
}
.week-container:last-child {
    border-right: 1px solid black;
}

/* display 2 week only */
.week-container {
    border-left: 1px solid black;
    border-bottom: 1px solid black;
    width: calc(100%/ 2);
}
.branch-container {
    display: flex;
    flex-direction: row;
}
.branch-develop,
.branch-release {
    display: flex;
    flex-direction: column;
}
.branch-develop {
    background-color: #3b3a3e;
    width: calc(100% / 10 * 5);
}
.branch-develop:first-child {
    width: calc(100% / 10 * 3);
}
.branch-develop:last-child {
    width: calc(100% / 10 * 2);
}
.branch-release {
    width: calc(100% / 10 * 5);
}
.action-container {
    display: flex;
    flex-direction: row;
}
.action-wrapper-develop,
.action-wrapper-release {
    display: flex;
    flex-direction: column;
}
.action-wrapper-develop {
    background-color: #60B99A;
    width: calc(100% / 10 * 5);
    justify-content: center;
}
.action-wrapper-develop:first-child {
    width: calc(100% / 10 * 3);
}
.action-wrapper-develop:last-child {
    width: calc(100% / 10 * 2);
}
.action-wrapper-release {
    background-color: #F1EFA5;
    width: calc(100% / 10 * 5);
}
.nested-action {
    display: flex;
    flex-wrap: wrap;
}
.nested-action .action:first-child {
    flex-basis: 100%;
}
.action.defects {
    width: calc(100% / 5 * 3);
    background-color: #D3CE3D;
}
.action.blockers {
    width: calc(100% / 5 * 2);
    background-color: #F77825;
}
.cert-container {
    display: flex;
    flex-direction: column;
}
.cert-branch {
    background-color: #464548;
    color: #e6e6e6;
}
.cert-branch p {
    margin: 6px 0;
    font-size: 12px;
}
body {
  font-family: 'Raleway', sans-serif;
}
.App {
  text-align: center;
  width: 80%;
  margin: 0 auto;
  font-size: 14px;
  position: relative;
}

.App-header {
  background-color: #313034;
  height: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  color: white;
}

